import React  from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Paper } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import './load.css';
const useStyles = makeStyles(theme => ({
  loadMain: {
    marginTop: '0px',
    padding: '0 20px',
    '& paper': {
      boxShadow: 'none',
      borderRadius: '0',
      border: '0',
      borderBottom: '1px solid #f2f2f2'
    }
  },
  root: {
    width: '100%',
    maxWidth: '100%',
    backgroundColor: ' ',
    padding: '0',
    display: 'flex',
    justifyContent: 'center',
    "&:hover": {
      //you want this to be the same as the backgroundColor above display:'inline-block',
      backgroundColor: "none"
    }
  },
  rootOne: {
    width: '100%',
    maxWidth: '100%',
    background: 'none',
    display: 'flex',
    color: '#868686',
    fontSize: '12px!important',
    "&:hover": {
      //you want this to be the same as the backgroundColor above display:'inline-block',
      backgroundColor: "none"
    },
    boxShadow: 'none',
    padding: '0',

  },
  paper: {
    width: '100%'
  },
  ListItem: {
    flexWrap: 'wrap',
    "&:hover": {
      //you want this to be the same as the backgroundColor above
      backgroundColor: "none"
    },
    paddingTop: '0'
  },
  heightOne: {
    height: '10px',
    padding: '0',
    margin: '0'
  }
  ,
  '& hr': {
    margin: theme.spacing(0, 0.5),
  },
  gridParent: {
    flexGrow: '1 1',
    marginBottom: '20px',
    border: 'none',
    boxShadow: 'none',
    width: '100%'
  },
  headingText: {
    fontSize: '12px',
  },
  topPaper: {
    padding: theme.spacing(2),
    textAlign: 'right',
    color: theme.palette.text.secondary,
    boxShadow: 'none',
    background: 'none'
  },
  alignLeft: {
    textAlign: 'left',
    boxShadow: 'none'
  },
  alignRight: {
    textAlign: 'right'
  },
  WrappedListItem: {
    flex: '0 0 73%',
    color: '#868686'
  },
  flexWrapParent: {

    flexWrap: 'wrap', "& :first-of-type ": {
      "& span": {
        fontWeight: '700',

      }
    }
  },
  maxWidth150: {
    maxWidth: '150px'
  },
 
  maxWidth84: {
    maxWidth: '84px'
  },
  WrappedListItemTop: {
    fontWeight: '700',
    flex: '0 0 53%'
  },
  greyText: {
    color: '#868686'
  },

  bottomAlignContent: {
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'flex-end'

  }
}));



export default function LoadListLargeSreen() {
  const classes = useStyles();
  
  const teamList = [1, 2, 3];
  const listItems = teamList.map(function (item) {
    return (

      <div key={item}>

        <Paper className={classes.root}>
          <List component="nav" className={classes.root} aria-label="mailbox folders">
            <ListItem className={classes.maxWidth84}>
              <ListItemText primary="01" />

              <Divider orientation="vertical" />
            </ListItem>
            <ListItem className={classes.flexWrapParent} >

              <ListItemText primary="Indranagar" className={classes.WrappedListItemTop} />
              <ListItemText primary="1st Block" className={classes.WrappedListItem} />
            </ListItem>

            <ListItem className={classes.flexWrapParent} >

              <ListItemText primary="Team Number" className={classes.WrappedListItemTop} />
              <ListItemText primary="Leader Name" className={classes.WrappedListItem} />
            </ListItem>

            <ListItem className={classes.alignRight} >

              <ListItemText primary="2220 Pts" />
            </ListItem>
          </List>

        </Paper>

        <div className={classes.heightOne}>
          &nbsp;
    </div>
      </div>
    );
  })
  return (
    <React.Fragment>
      <div className={classes.gridParent}>
        <Grid container spacing={3} >

          <Grid item xs={12} sm={6} style={{paddingBottom:'0'}}>
            <ListItem className={classes.flexWrapParent} style={{paddingBottom:'0'}} >

              <ListItemText primary="Name" className={classes.WrappedListItemTop} />
              <ListItemText primary="Vehicle Name" className={classes.WrappedListItem} />
            </ListItem>  </Grid>
          <Grid item xs={12} sm={6} className={[classes.alignRight, classes.bottomAlignContent].join(' ')}>
            <Typography>Total lead Points</Typography>
          </Grid>

        </Grid>

      </div>
      <Paper className={classes.loadMain} id="load-main">
        <div className={classes.gridParent}>
          <Grid container spacing={3} className={" border-bottom-grey "}>

            <Grid item xs={12} sm={6}>
              <ListItem className={classes.flexWrapParent} >

                <ListItemText primary="Team Number" className={classes.WrappedListItemTop} />
                <ListItemText primary="Leader Name" className={classes.WrappedListItem} />
              </ListItem>  </Grid>
            <Grid item xs={12} sm={6} className={classes.alignRight}>
              <Typography>8000 Pts</Typography>
            </Grid>

          </Grid>

        </div>
        <Paper className={classes.rootOne}>
          <List component="nav" className={classes.rootOne} aria-label="mailbox folders">
            <ListItem className={classes.maxWidth150} variant="raised">
              <ListItemText className={classes.headingText} primary="Green Cluster" />
            </ListItem>
            <ListItem variant="raised">
              <ListItemText className={classes.headingText} primary="  " />
            </ListItem>

            <ListItem variant="raised">
              <ListItemText className={classes.headingText} primary="Feed Team" />
            </ListItem>

            <ListItem variant="raised" className={classes.alignRight} >
              <ListItemText className={classes.headingText} primary="Total Points" />
            </ListItem>

          </List>
        </Paper>
        {listItems}
        <Grid container spacing={3}>

          <Grid item xs={12} sm={6}>
            <Paper className={[classes.topPaper, classes.alignLeft].join(' ')}>
              <Button variant="outlined" color="primary" className={classes.button}>
                REPORT
      </Button>

            </Paper>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Paper className={classes.topPaper}> Total points to send: 8000 Pts
  <Button variant="contained" color="primary" className={classes.button} style={{ marginLeft: '10px' }}>
                SEND POINTS
      </Button>
            </Paper>
          </Grid>

        </Grid>
      </Paper>
    </React.Fragment>
  );
}