import React, { Component }  from 'react'; 
import LoadListLargeSreen from './LoadListLargeSreen'
import LoadListSmallScreen from './LoadListSmallScreen'

export default class LoadMain extends Component  {
  render() {
        let width = window.innerWidth;  
    if (width > 768) {
      return (
       <LoadListLargeSreen></LoadListLargeSreen>
       
       );
    } else {
      return ( 
    <LoadListSmallScreen></LoadListSmallScreen>
      );
    }
 }
 
}
